import "./styles.css";
import { useState } from "react";

export default function App() {
  // const [initial, setInitial] = useState("");
  const [update, setUpdated] = useState(number);

  function handleInput() {
    //please put your logic here
    // const result = reverseInput()
    // console.log("Result : " + result);
    // setUpdated(number);
    setUpdated(number);
    function reverseInput(n) {
      return parseInt(n.toString().split("").reverse().join("")) * Math.sign(n);
    }
    return reverseInput;
  }

  // render() {
  return (
    <div className="App">
      <div>
        Number: <input type="number" id="number" value="number" required />
        <button type="submit" onClick={handleInput}>
          Submit
        </button>
      </div>
      <div>Result: {update}</div>
    </div>
  );
  // };
}
